.info{
  padding: 5em 0;
  &__items{
    display: flex;
    flex-wrap: wrap;
    width: calc(100% + 6.25em);
  }
}

.info-card{
  margin: 3.125em 5.25em 0 0;
  padding-left: 3.9375em;
  position: relative;
  @media screen and (min-width: 993px){
    &:nth-child(3){
      margin-left: 6.25em;
    }
    &:nth-child(5){
      margin-left: auto;
      margin-right: auto;
    }
  }
  &__inner{
    padding: 2.1875em 2.625em 2.1875em 6.25em;
    height: 7.875em;
    border-radius: 0 6.875em 6.875em 0;
    box-shadow: 2px 2px 20px 0 rgba(0, 0, 0, 0.25);
    background: linear-gradient(93deg, #fff 0%, #ecebfe 100%);
    color: $black;
    font-weight: 600;
    display: flex;
    align-items: center;
  }
  &__icon{
    left: 0;
    top: 50%;
    transform: translateY(-50%);
    border-radius: 100%;
    background: linear-gradient(131deg, #e1e0fc 0%, #fffeff 100%);
    box-shadow: 2px 2px 15px 0 rgba(0, 0, 0, 0.25);
    width: 7.875em;
    height: 7.875em;
    position: absolute;
    padding: .5em;
    @include center;
    img{
      max-width: 100%;
      max-height: 100%;
    }
  }
}

.advantages{
  padding: 4.375em 0;
  background-size: cover;
  background-position: center;
  .container{
    position: relative;
    z-index: 2;
  }
  &__items{
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    margin-top: 4em;
  }
}

.advantage-card{
  margin: 1.75em 4.25em 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 13.625em;
  text-align: center;
  &__icon{
    width: 13em;
    height: 13em;
    background-color: #fff;
    @include center;
    border-radius: 50%;
    margin-bottom: 1.5em;
    img{
      max-width: 100%;
      max-height: 100%;
    }
  }
}


@media screen and (max-width: 992px){
  .info{
    padding: 1.5em 0;
    &__items{
      display: block;
      width: 100%;
      margin-top: 1em;
    }
  }

  .info-card{
    width: 100%;
    margin: 1em 0 0;
    br{
      display: none;
    }
  }

  .advantages{
    padding: 1.5em 0;
    &__items{
      margin-top: 1em;
    }
  }

  .advantage-card{
    margin: 1em 2em 0;
  }
}


@media screen and (max-width: 576px){
  .info-card{
    font-size: .625em;
    &__inner{
      padding-left: 5em;
    }
    .text20{
      //font-size: 1em;
    }
  }

  .advantages{
    &__items{
      width: calc(100% + 1em);
    }
  }

  .advantage-card{
    margin: 1em 1em 0 0;
    width: calc(50% - 1em);
    &__icon{
      width: 8em;
      height: 8em;
      margin-bottom: .75em;
      filter: drop-shadow(0 0 15px rgba(#000, .4));
    }
    &__text{
      font-size: 1em;
      margin: 0 auto;
      max-width: 12em;
    }
  }
}
