.footer{
  padding: 3.125em 0 4em;
  &__inner{
    display: flex;
    justify-content: flex-end;
  }
  &__info{
    margin-right: auto;
    max-width: 21.375em;
  }
  &__logo{
    width: 20.75em;
    display: block;
    img{
      width: 100%;
      display: block;
    }
  }
  &__desc{
    padding-left: 5.375em;
  }
  &__contacts,
  &__links{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
  &__contacts{
    max-width: 18.4375em;
  }
  &__btn{
    height: 2.8125em;
    padding: 0 1em;
    margin-top: .625em;
    span{
      font-size: 1em;
    }
    img{
      width: .9375em;
      margin-right: .3125em;
    }
  }
  &__links{
    margin-left: 3.5em;
  }
  &__link{
    font-weight: 500;
    @include lastM(0 0 .625em);
    span{
      font-size: 1.25em;
      line-height: 125%;
    }
  }
}


@media screen and (max-width: 992px){
  .footer{
    padding: 2em 0;
    &__inner{
      @include grid100gap(1.5em);
    }
    &__info{
      max-width: 100%;
    }
    &__logo{
      width: 10em;
    }
    &__desc{
      display: none;
    }
    &__links{
      margin: 0;
    }
    &__link{
      span{
        font-size: 1em;
      }
    }
    &__contacts{
      order: 2;
      max-width: 100%;
    }
  }
}


@media screen and (max-width: 576px){
  .footer{
    &__btn{
      width: 100%;
    }
  }
}
