@import 'vars';
@import 'components/_text.scss';
@import 'components/_buttons.scss';
@import "components/_inputs.scss";
@import "components/_pagination.scss";
@import "components/_tabs.scss";
@import "components/_grid.scss";
.socials{
  display: flex;
  align-items: center;
  &__item{
    width: 3.125em;
    height: 3.125em;
    border-radius: 50%;
    background: $primary;
    @include lastM(0 1.25em 0 0);
    @include center;
    &:hover{
      background: darken($primary,10%);
    }
    img{
      width: 1.875em;
    }
  }
}

.m-backdrop{
  position: relative;
  &:after{
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: #000;
    opacity: .6;
    pointer-events: none;
    border-radius: inherit;
  }
}
