.text{
  font-weight: 500;
  li,
  p{
    font-size: 1.25em;
    line-height: 125%;
  }
  ol,
  ul,
  p{
    @include lastM(0 0 1em);
  }
  b{
    font-weight: 700;
  }
  hr{
    height: 3.25em;
    opacity: 0;
    display: block;
  }
  ul,ol{
    @include grid100gap(.5em);
    &>li{
      position: relative;
      padding-left: 1.5em;
      &:before{
        position: absolute;
        top: 0;
        left: 0;
        width: 1.5em;
        text-align: center;
      }
    }
    ul,ol{
      margin: .625em 0;
      li{
        font-size: 1em;
      }
    }
  }
  ul{
    &>li{
      &:before{
        content: '•';
      }
    }
  }
  ol{
    counter-reset: list;
    &>li{
      counter-increment: list;
      &:before{
        content: counter(list) ".";
      }
    }
  }
  blockquote{
    @include lastM(0 0 1.25em);
    border-left: .3125em solid $primary;
    padding: 1.875em 1.25em 1.875em 5em;
    position: relative;
    background: rgba(85, 55, 29, 0.4);
    font-style: italic;
    &:before{
      content: '';
      position: absolute;
      top: 1.1875em;
      left: 1.25em;
      background-image: url(../img/quote.svg);
      @include bgImgSize(100%);
      width: 2.5625em;
      height: 2em;
    }
    p{
      font-size: 1em;
      line-height: 120%;
      font-weight: 300;
    }
    b{
      font-weight: 800;
    }
  }
  a{
    text-decoration: underline;
    color: $primary;
  }
}

@media screen and (max-width: 992px){
  .text{
    font-size: .875em;
  }
}


@media screen and (max-width: 576px){
  .text{
    font-size: .75em;
  }
}
