.catalog{
  padding: 4.375em 0 9.375em;
  &__top{
    margin-bottom: 4.375em;
  }
}

.catalog-card{
  display: block;
  &__img{
    @include imgCover(64.3%);
    border-radius: 1.25em;
    margin-bottom: .625em;
  }
  iframe{
    border-radius: 0;
  }
}

.more{
  margin-top: 4.375em;
  display: flex;
  justify-content: center;
}

.text-link{
  font-weight: 500;
  font-size: 1.25em;
  line-height: 124%;
  text-decoration: underline;
  text-decoration-skip-ink: none;
}

.contacts{
  position: relative;
}

.map{
  width: 100%;
  height: 37.5em;
}

.contacts-info{
  position: absolute;
  left: 5em;
  top: 50%;
  transform: translateY(-50%);
  padding: 1.25em;
  background: rgba(0, 0, 0, 0.8);
  width: 31.625em;
  border-radius: 1.25em;
  @include grid100gap(.5em);
  @media screen and (min-width: 1361px) {
    left: calc((100% - 1360px) / 2 + 5em);
  }
}

.sales{
  padding: 4.375em 0;
  &__grid{
    margin-top: 4.375em;
    display: grid;
    grid-template-columns: calc(100% - 28.9375em - 3.4375em) 28.9375em;
    justify-content: space-between;
    gap: 1.25em;
  }
  &__img{
    display: block;
    width: 100%;
    margin-top: -6em;
  }
}

.sale-card{
  border-radius: 1.25em;
  position: relative;
  padding-bottom: 55.8%;
  background-image: url(../img/card.webp);
  background-size: cover;
  background-position: center;
  &__top,
  &__bottom{
    position: absolute;
    padding: 2em 1.875em;
    left: 0;
    width: 100%;
  }
  &__top{
    top: 0;
    color: $primary;
    font-weight: 600;
    max-width: 30em;
    span{
      font-size: 2.8125em;
      line-height: 120%;
    }
  }
  &__bottom{
    bottom: 0;
    text-align: right;
    .h1{
      font-weight: 900;
    }
    span{
      font-size: .75em;
    }
  }
}


@media screen and (max-width: 992px){
  .catalog{
    padding: 1.5em 0 3em;
    &__top{
      margin-bottom: 1.25em;
    }
  }

  .catalog-card{
    &__img{
      border-radius: .5em;
    }
  }

  .more{
    margin-top: 1.5em;
  }

  .contacts-info{
    position: static;
    transform: unset;
    padding: 1.25em 1em;
    width: 100%;
    border-radius: 0;
  }

  .map{
    height: 25em;
  }

  .sales{
    padding: 1.5em 0;
    &__grid{
      margin-top: 1.5em;
      grid-template-columns: 1.5fr 1fr;
    }
    &__img{
      margin-top: -3em;
    }
  }

  .sale-card{
    border-radius: .625em;
    &__top,
    &__bottom{
      padding: 1em;
    }
    &__top{
      max-width: 20em;
      span{
        font-size: 1.75em;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .map{
    height: 20em;
  }

  .sales{
    &__grid{
      grid-template-columns: 100%;
    }
    &__img{
      margin-top: 0;
    }
  }

  .sale-card{
    font-size: .8125em;
  }
}
