.breadcrumbs {
    &__item {
        position: relative;
        display: inline;
        font-size: 1.25em;
        line-height: 125%;
        color: $gray;
        font-weight: 300;
        &:not(:first-child) {
            margin-left: .75em;
            &:before {
                content: '-';
                position: absolute;
                z-index: 1;
                top: -.03125em;
                right: 100%;
                margin-right: .25em;
                color: $gray;
            }
        }
        &:last-child{
            pointer-events: none;
            color: #fff;
        }
    }
}
