@import "../vars";
.m-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    padding: 0 2em;
    height: 4.375em;
    border-radius: 6.25em;
    font-weight: 500;
    &:focus {
        box-shadow: none;
    }
    span{
        font-size: 1.25em;
        line-height: 120%;
    }
}

.m-btn-primary{
    background-color: $primary;
    color: #fff;
    &:hover {
        background-color: darken($primary, 10%);
        color: #fff;
    }
}

.m-btn-dark{
    background-color: #55371D;
    color: #fff;
    &:hover{
        background-color: darken(#55371D,5%);
        color: #fff;
    }
}

.m-btn-white{
    background-color: #fff;
    color: #000;
    &:hover{
        background-color: darken(#fff,10%);
        color: #000;
    }
}


@media screen and (max-width: 992px){
    .m-btn{
        height: 3.5em;
    }
}
