.fg {
  position: relative;
  input,
  textarea{
    border-radius: 6.25em;
    border: 1px solid #fff;
    padding: 0 1.25em;
    height: 4.375em;
    width: 100%;
    font-size: 1em;
    background-color: transparent;
    color: #fff;
    &::placeholder {
      color: $gray;
    }
    &:focus {
      border-color: $primary;
    }
  }
  textarea{
    padding-top: 1.19em;
    padding-bottom: 1.19em;
    height: 13.0625em;
    resize: vertical;
  }
}

.pass-toggle{
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  right: 1.25em;
  width: 1.5em;
  height: 1.5em;
  img{
    @include centerAbsolute;
    width: 100%;
  }
  ._view2{
    opacity: 0;
  }
  &._active{
    ._view2{
      opacity: 1;
    }
    ._view1{
      opacity: 0;
    }
  }
}

.m-radio,
.m-check{
  position: relative;
  input{
    position: absolute;
    opacity: 0;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    z-index: 2;
    cursor: pointer;
  }
}

.m-check,
.m-radio{
  label{
    position: relative;
    cursor: pointer;
    display: flex;
    align-items: center;
    &:before,
    &:after{
      pointer-events: none;
    }
    &:before{
      content: '';
      position: absolute;
      z-index: 1;
      left: 0;
      top: 50%;
      transform: translateY(-50%);
      transition: all .0s;
    }
    a{
      text-decoration: underline;
      position: relative;
      z-index: 2;
    }
  }
}

.m-check {
  input {
    &:checked+label {
      &::before {
        background-size: .625em auto;
      }
    }
  }
  label {
    padding-left: 1.5em;
    min-height: .875em;
    padding-top: .0625em;
    span{
      font-size: .9375em;
      line-height: 140%;
    }
    &:before {
      border: 1px solid #fff;
      width: .875em;
      height: .875em;
      @include bgImgSize(0);
      background-image: url(../img/check.svg);
    }
  }

}

.m-radio{
  input:checked~label{
    &:before{
      background-size: 100% auto;
      border-width: .5em;
    }
  }
  label{
    font-weight: 500;
    min-height: 1.9375em;
    padding-left: 2.625em;
    &:before{
      border-radius: 50%;
      width: 1.9375em;
      height: 1.9375em;
      border: 1px solid $primary;
    }
    span{
      display: block;
      font-size: 1.0625em;
      line-height: 157%;
      margin-top: .125em;
    }
  }
}

.m-select{
  position: relative;
  &._open{
    .m-select__dropdown{
      opacity: 1;
      pointer-events: unset;
    }
  }
  &._not-select{
    .m-select__toggle{
      color: $gray;
      font-weight: 300;
    }
  }
  &__toggle{
    display: flex;
    align-items: center;
    justify-content: space-between;
    transition: all .3s;
    cursor: pointer;
    padding: 0 .625em;
    border: 1px solid #fff;
    border-radius: .25em;
    height: 3.125em;
    &:hover{
      color: $primary;
      &>svg path{
        fill: $primary;
      }
    }
    &>svg{
      width: .625em;
      margin-left: .75em;
    }
  }
  &__current{
    font-weight: 500;
    font-size: .9375em;
  }
  &__dropdown{
    transition: all .3s;
    opacity: 0;
    pointer-events: none;
    border-radius: 0.375em;
    border: 1px solid #fff;
    background-color: $black;
    min-width: 100%;
    padding: .5em 0;
    position: absolute;
    top: 100%;
    left: 0;
    margin-top: .25em;
    z-index: 5;
  }
  &__option{
    padding: .5em .625em;
    display: flex;
    align-items: center;
    transition: all .3s;
    cursor: pointer;
    min-height: 2.1875em;
    &:hover,
    &._active{
      background-color: $primary;
    }
    span{
      font-size: 1em;
      line-height: 110%;
    }
  }
  .m-check{
    label{
      &:before{
        transform: translateY(8%);
      }
    }
  }
}


@media screen and (max-width: 992px){
  .fg{
    input{
      height: 3.5em;
    }
  }
}


