.about{
  padding: 5em 0 10em;
  .h1{
    color: $primary;
  }
  &__grid{
    display: grid;
    grid-template-columns: 36.875em calc(100% - 41.875em);
    gap: 5em;
    margin-top: 4em;
  }
  &__text{
    font-weight: 500;
    b{
      font-size: 1.25em;
      line-height: 99%;
      color: $primary;
      font-weight: 800;
    }
  }
  &__media{
    position: relative;
  }
  &__img{
    img{
      width: 100%;
      display: block;
    }
    @media screen and (min-width: 577px){
      &--float{
        position: absolute;
        padding: .5em .5em 0 0;
        width: 20em;
        background-color: $black;
        bottom: -3.5em;
        left: -4.5em;
      }
    }
  }
  .video{
    margin-top: 5em;
  }
}

.video{
  display: block;
  position: relative;
  &__play{
    @include centerAbsolute;
    width: 7.0625em;
  }
  &__img{
    &:after{
      opacity: .42;
    }
    img{
      width: 100%;
      display: block;
    }
  }
}

.ui{
  padding: 5em 0 10em;
  &__buttons{
    @include grid100gap(1.875em);
  }
  &__checks{
    @include grid100gap(1.25em);
  }
}

._split{
  height: 5em;
}

.m-form{
  padding: 1.5em 2.875em;
  box-shadow: 0 7px 25px 0 rgba(0, 0, 0, 0.6);
  background: #1e1e1e;
  border-radius: 1.25em;
  &__fields{
    margin-top: 1.5em;
    @include grid100gap(1.25em);
  }
  &__check{
    display: flex;
    justify-content: center;
  }
}


@media screen and (max-width: 992px){
  .about{
    padding: 1.5em 0 5em;
    &__grid{
      margin-top: 1.25em;
      grid-template-columns: 100%;
      gap: 1.5em;
    }
    &__media{
      padding-left: 5em;
    }
    &__img{
      &--float{
        left: 0;
      }
    }
  }
}

.video{
  &__play{
    width: 4em;
  }
}


@media screen and (max-width: 576px){
  .about{
    padding-bottom: 3em;
    &__media{
      padding-left: 0;
    }
    &__img{
      &--float{
        margin-top: 1em;
      }
    }
    .video{
      margin-top: 2em;
    }
  }

  .m-form{
    padding: 1em;
    &__fields{
      gap: 1em;
    }
  }
}
