.header{
  background-color: #000000;
  position: relative;
  padding-bottom: 3.125em;
  z-index: 20;
  &__inner{
    padding: 2.1875em 0 .6875em;
    display: flex;
  }
  &__logo{
    width: 12.3125em;
    display: block;
    flex-shrink: 0;
    margin-right: 1.25em;
    img{
      width: 100%;
      display: block;
    }
  }
}

.burger{
  display: none;
}

.menu{
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-grow: 1;
  &__time{
    max-width: 7.625em;
    margin-right: 1.25em;
  }
  &__street{
    max-width: 16.875em;
    img{
      width: 2.125em;
      margin-right: .3125em;
    }
  }
  &__btn{
    height: 2.8125em;
    padding: 0 1em;
    margin-left: 1.25em;
    span{
      font-size: 1em;
    }
    img{
      width: .9375em;
      margin-right: .3125em;
    }
  }
  &__links{
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    background-color: $primary;
  }
}

._flex{
  display: flex;
  align-items: center;
}

.nav{
  @include center;
  height: 3.125em;
  &__show,
  &__link{
    font-weight: 500;
    span{
      font-size: 1.25em;
      line-height: 120%;
    }
    &:hover{
      color: rgba(#fff,.8);
    }
  }
  &__item,
  &__link{
    @include lastM(0 3.375em 0 0);
  }
  &__show{
    display: flex;
    align-items: flex-end;
    transition: all .3s;
    cursor: pointer;
    img{
      width: 1.25em;
      margin-left: .3125em;
      transition: all .3s;
    }
  }
  &__item{
    position: relative;
    &:hover{
      .nav__hidden{
        opacity: 1;
        pointer-events: unset;
      }
    }
  }
  @media screen and (min-width: 993px){
    &__hidden{
      position: absolute;
      top: 100%;
      left: 50%;
      transform: translateX(-50%);
      margin-top: -1px;
      padding-top: 1.125em;
      opacity: 0;
      pointer-events: none;
      transition: all .3s;
    }
    &__inner{
      min-width: 10.75em;
      box-shadow: 2px 2px 20px 0 rgba(0, 0, 0, 0.25);
      background: #fff;
      border-radius: 1.25em;
      color: $black;
      padding: 1.25em .6875em;
      align-items: center;
      white-space: nowrap;
    }
  }
  &__inner{
    display: flex;
    flex-direction: column;
  }
  &__sublink{
    font-size: 1em;
    line-height: 150%;
    font-weight: 500;
    @include lastM(0 0 .625em);
  }
}


@media screen and (max-width: 992px){
  .header{
    padding: 1.25em 0;
    &__inner{
      align-items: center;
      padding: 0;
      justify-content: space-between;
    }
    &__logo{
      width: 10em;
      position: relative;
      z-index: 11;
    }
    &._open{
      .menu{
        opacity: 1;
        pointer-events: unset;
      }
      .burger{
        ._view1{
          opacity: 0;
        }
        ._view2{
          opacity: 1;
        }
      }
    }
  }

  .menu{
    transition: all .3s;
    opacity: 0;
    pointer-events: none;
    background-color: #000;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: $vh;
    flex-direction: column;
    padding: 6em 1em 2em;
    align-items: flex-start;
    justify-content: unset;
    &__right{
      flex-direction: column;
      align-items: flex-start;
      margin-top: 2em;
      width: 100%;
    }
    &__left{
      margin-top: 4em;
    }
    &__links{
      background-color: transparent;
      position: static;
      .container{
        padding: 0;
      }
    }
    &__btn{
      margin: 1em 0 0;
    }
  }

  .nav{
    flex-direction: column;
    align-items: flex-start;
    height: unset;
    &__item,
    &__link{
      @include lastM(0 0 1.25em);
    }
    &__show{
      img{
        display: none;
      }
    }
    &__inner{
      align-items: flex-start;
      padding: 1em 0 0 1em;
    }
  }

  .burger{
    z-index: 11;
    position: relative;
    width: 2em;
    height: 2em;
    display: flex;
    img{
      @include centerAbsolute;
      width: 100%;
      transition: inherit;
    }
    ._view2{
      opacity: 0;
      width: 2.25em;
    }
  }
}


@media screen and (max-width: 576px){
  .menu{
    &__btn{
      width: 100%;
    }
  }
}
