.slider{
  position: relative;
}

.main-slider{
  overflow: hidden;
  &__img{
    @include imgCover(43.4%);
  }
  .swiper{
    &-button{
      &-prev{
        left: 1.875em;
      }
      &-next{
        right: 1.875em;
      }
      @media screen and (min-width: 1361px){
        &-prev{
          left: calc((100% - 1360px) / 2 + 1.875em);
        }
        &-next{
          right: calc((100% - 1360px) / 2 + 1.875em);
        }
      }
    }
    &-pagination{
      bottom: .9375em;
    }
  }
}

.swiper-button{
  width: 2.1875em;
  height: 2.1875em;
  @include center;
  border-radius: 50%;
  img{
    width: 1.1875em;
  }
}

.swiper-pagination{
  &-bullet{
    width: .9375em;
    height: .9375em;
    background-color: transparent;
    border: 1px solid $primary;
    opacity: 1;
    margin: 0 .3125em !important;
    &-active{
      background-color: $primary;
    }
  }
}

.gallery{
  padding: 4.375em 0;
  overflow: hidden;
}

.gallery-slider{
  margin-top: 4.375em;
  padding: 0 3.125em;
  .swiper{
    &-container{
      width: calc(100% + 6.25em);
    }
    &-slide{
      padding-right: 6.25em;
      width: calc(100% / 3);
      opacity: .3;
      transition: all .3s;
      &-active,
      &-next{
        opacity: 1;
      }
      @media screen and (min-width: 577px){
        &-next+.swiper-slide{
          opacity: 1;
        }
      }
    }
    &-button{
      &-prev{
        left: 0;
      }
      &-next{
        right: 0;
      }
    }
  }
}

.gallery-item{
  display: block;
  @include imgCover(133%);
  border-radius: 1.25em;
  box-shadow: 2px 2px 20px 0 rgba(0, 0, 0, 0.25);
}


@media screen and (max-width: 992px){
  .main-slider{
    .swiper {
      &-button {
        &-prev {
          left: .5em;
        }

        &-next {
          right: .5em;
        }
      }
    }
  }

  .gallery{
    padding: 1.5em 0;
  }

  .gallery-slider{
    padding: 0;
    margin-top: 1.25em;
    .swiper{
      &-container{
        width: calc(100% + 1.25em);
      }
      &-slide{
        padding-right: 1.25em;
      }
      &-button{
        &-prev{
          left: -.5em;
        }
        &-next{
          right: -.5em;
        }
      }
    }
  }

  .gallery-item{
    border-radius: .5em;
  }
}


@media screen and (max-width: 576px){
  .main-slider{
    &__img{
      padding-bottom: 75%;
    }
  }

  .gallery-slider{
    margin-top: 1.5em;
    .swiper{
      &-container{
        width: calc(100% + .625em);
      }
      &-slide{
        width: 50%;
        padding-right: .625em;
      }
    }
  }
}
